.nav {

    margin-left: 0;
    margin-right: 0;
    position: sticky;
    display: block;
    align-items: center;
    height: 100vh;
    background-color: #0b0b13;
    border-right: 1px solid hsla(0,0%,100%,.1);
}

.head_logo {

}

.head_img {
    width: 80px;
}

.head_link {

    text-decoration: none;
    color: white;
    line-height: 80px;
    display: inline-block;
    vertical-align: top;
    text-align: center;
}

.logo_link {

}

.logo_link img {

}

.nav_menu {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    padding-top: 25px;
    padding-bottom: 45px;
    list-style-type: none;

}

.nav_menu li {
    display: block;
    border-bottom: 1px solid hsla(0,0%,100%,.1);
    width:100%;
    text-align: center;
}


.nav_link {
    font-family: "Rubik", monospace;
    font-size: 25px;
    font-weight: 300;
    line-height: 60px;
    color: white;
    text-decoration: none;


}

.nav_link:hover {
    color:orange;
}