.main {
    display: block;
    font-family: "Rubik", monospace;
    line-height: 1.7;
    padding: 12px;
    height: 100vh;

}

.header_info {


}

.header_links {
    display: flex;
    font-size: 14px;
    font-weight: 400;
    color: whitesmoke;
    list-style-type: none;
    padding-left: 50px;
}

.header_links li {
    padding-right: 50px;

}

li a {
    text-decoration: none;
    color: whitesmoke;
}

.header_link:hover {
    color: orange;
}

.main_banner {

    padding-top: 120px;
    padding-bottom: 120px;
    padding-left: 70px;
    max-width: 450px;
}

.main_info {
    color: whitesmoke;

}

.main_info h6 {
    font-size: 18px;
    font-weight: 400;
    margin: 0 0 10px;
}

.main_info h1 {
    font-size:36px;
    font-weight: 700;
    margin-left: 50px;
}

.animated {
    font-size: 25px;
    font-weight: 400;
    color:orange;
}

.animated:before {
    content: "";
    animation: topToBottom 10s infinite 0s;
}

@keyframes topToBottom {
    0% {
        content: "Full-Stack developer ";
    }


    50% {
        content: "Node JS";
    }

    100% {
        content: "React";
    }
}

.main_info p {
    font-size: 15px;
    font-weight: 200;
}

.main_header {

}

.main_wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}



.img_wrapper {
width: 100%;
}


